* {
  font-family: Avenir,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
  letter-spacing: .5px
}

.ce-chat-list,.ce-chats-container {
  background-color: #f0f0f0!important
}

.ce-active-chat-card {
  background-color: #cabcdc!important;
  border: 4px solid #cabcdc!important;
  border-radius: 0!important
}

.ce-chat-subtitle-text {
  color: #595959!important
}

.ce-chat-form-container {
  padding-bottom: 20px!important
}

.ce-text-input {
  border-radius: 6px!important;
  border: 1px solid #3b2a50!important
}

.ce-primary-button {
  border-radius: 6px!important;
  background-color: #7554a0!important;
  position: relative;
  bottom: 1px
}

.ce-danger-button {
  background-color: #fff!important;
  border-radius: 22px!important
}

.ce-settings {
  background-color: #f0f0f0!important
}

.ce-autocomplete-input,.ce-autocomplete-options {
  border-radius: 6px!important;
  border: 1px solid #3b2a50!important
}

.ce-autocomplete-options {
  background-color: #fff!important
}

.ce-chat-settings-container {
  padding-top: 12px!important
}

.ce-chat-avatars-row {
  display: none!important
}

.chat-feed {
  height: 100%;
  width: 100%;
  overflow: scroll;
  background-color: #f0f0f0
}

.chat-title-container {
  width: calc(100% - 36px);
  padding: 18px;
  text-align: center
}

.chat-title {
  color: #7554a0;
  font-weight: 800;
  font-size: 24px
}

.chat-subtitle {
  color: #7554a0;
  font-weight: 600;
  font-size: 12px;
  padding-top: 4px
}

.message-row {
  float: left;
  width: 100%;
  display: flex;
  margin-left: 18px
}

.message-block {
  width: 100%;
  display: inline-block
}

.message-avatar {
  width: 44px;
  height: 44px;
  border-radius: 22px;
  color: #fff;
  text-align: center;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 48px
}

.message {
  padding: 12px;
  font-size: 16px;
  border-radius: 6px;
  max-width: 60%
}

.message-image {
  margin-right: 18px;
  object-fit: cover;
  border-radius: 6px;
  height: 30vw;
  max-height: 200px;
  max-width: 200px;
  min-height: 100px;
  min-width: 100px
}

.read-receipts {
  position: relative;
  bottom: 6px
}

.read-receipt {
  width: 13px;
  height: 13px;
  border-radius: 13px;
  margin: 1.5px;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 14px
}

.message-form-container {
  position: absolute;
  bottom: 0;
  width: calc(100% - 36px);
  padding: 18px;
  background-color: #f0f0f0
}

.message-form {
  overflow: hidden;
  border-radius: 6px;
  border: 1px solid #3b2a50;
  background-color: #fff
}

.message-input {
  height: 40px;
  width: calc(100% - 132px);
  background-color: #fff;
  border: 1px solid #fff;
  padding: 0 18px;
  outline: none;
  font-size: 15px
}

.image-button {
  cursor: pointer;
  padding: 0 12px;
  height: 100%
}

.send-button {
  height: 42px;
  background-color: #fff;
  border: 1px solid #fff;
  padding: 0 18px;
  cursor: pointer
}

.send-icon {
  transform: rotate(-90deg)
}

.picture-icon,.send-icon {
  top: 1px;
  position: relative
}

.picture-icon {
  font-size: 14px
}

*,:after,:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 62,5%
}

.wrapper {
  height: 100vh;
  width: 100%;
  background: #7554a0;
  background: linear-gradient(90deg,#7554a0 7%,#7554a0 17%,#6a5fa8 29%,#6367ae 44%,#5774b8 66%,#4687c6 83%,#2ca3db 96%,#16bced 100%,#00d4ff 0);
  display: flex;
  justify-content: center;
  align-items: center
}

.input {
  color: #333;
  font-size: 1.2rem;
  padding: 1.5rem 2rem;
  border-radius: .2rem;
  background-color: #fff;
  width: 90%;
  display: block;
  border: none;
  border-bottom: .3rem solid transparent;
  transition: all .3s;
  outline: none;
  margin: 0 auto 25px
}

.form {
  width: 400px
}

.title {
  color: #fff;
  margin-bottom: 30px;
  width: 100%;
  font-size: 2.3em
}

.button,.title {
  text-align: center
}

.button {
  border-radius: 4px;
  border: none;
  background-color: #fff;
  color: #000;
  text-transform: uppercase;
  font-size: 22px;
  padding: 20px;
  width: 200px;
  transition: all .4s;
  cursor: pointer;
  margin: 5px;
  width: 90%
}

.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: .4s
}

.button span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: .5s
}

.button:hover span {
  padding-right: 25px
}

.button:hover span:after {
  opacity: 1;
  right: 0
}

